green: '#0EB192'
gold: '#C6B378' */

.carRental {
  height: auto;
  overflow-x: hidden;
}

.rentalTop {
  background-image: linear-gradient(
      rgba(29, 19, 1, 0.658),
      rgba(29, 19, 1, 0.658)
    ),
    url("https://media-cldnry.s-nbcnews.com/image/upload/newscms/2019_13/2798361/190325-rental-cars-cs-229p.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

.topImg {
  width: 100vw;
  height: 100px;
}
.topImg > img {
  width: 100%;
  height: 120%;
  object-fit: contain;
  animation: slide-fly 1s linear;
  transition: all 1s ease;
}
@keyframes slide-fly {
  from {
    transform: translateX(100px);
    opacity: 0.2;
  }
}

.topBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100% - 100px);
  color: white;
  animation: flyup 1s linear;
  transition: all 1s ease;
}

@keyframes flyup {
  from {
    transform: translateY(100px);
    opacity: 0.2;
  }
}

.topBody > h1 {
  font-size: 5em;
  text-align: center;
}
.topBody > p {
  width: 60%;
  text-align: center;
  font-size: 1.5em;
}

.form {
  width: 100%;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: whitesmoke;
}

.reservation {
  text-transform: uppercase;
  font-size: 2em;
  margin-bottom: 20px;
  color: #f04527;
}

.row {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.col {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-bottom: 10px;
}
input {
  height: 30px;
  padding: 0 10px;
  background-color: transparent;
  border: 1px solid lightgray;
  color: gray;
}
input:focus {
  outline: none;
}

select{
  height: 30px;
  padding: 0 10px;
  background-color: transparent;
  border: 1px solid lightgray;
  color: gray;
  width: 50%;
}
select:focus{
  outline: none;
}
option{
  height: 30px;
  font-size: 16px;
}

.label {
  font-size: 18px;
  font-weight: bold;
  color: #f57325;
}

label {
  color: gray;
}

.radio {
  display: flex;
  align-items: center;
  gap: 10px;
}

.subtotal > input {
  border: none;
  border-bottom: 1px solid lightgray;
}

.subtotal > input:focus {
  outline: none;
}

.submit {
  border: none;
  background-color: #f57325;
  color: white;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 18px;
  width: 50%;
  position: relative;
}

.tooltip {
  position: absolute;
  margin-left: 30px;
  margin-top: 5px;
  background-color: rgba(0, 0, 0, 0.788);
  color: white;
  text-align: center;
  padding: 4px;
  width: 70%;
  visibility: hidden;
  border-radius: 4px;
  font-size: 16px;
}

.submit:disabled {
  background-color: #f5752591;
  cursor: default;
}
.submit:disabled:hover .tooltip {
  visibility: visible;
}

h3 {
  margin-top: 20px;
  color: #f04527;
  padding: 10px;
}

.footer {
  background-color: #f57325;
  opacity: 0.7;
  text-align: center;
  height: 40px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

@media screen and (max-width: 1200px) {
  .topBody > h1 {
    font-size: 3em;
    width: 90%;
  }
  .topBody > p {
    font-size: 1.4em;
    width: 95%;
    margin-top: 20px;
  }
  .submit {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .topBody > h1 {
    font-size: 2.5em;
    width: 96%;
  }
  .topBody > p {
    font-size: 1.2em;
    width: 95%;
    margin-top: 20px;
  }

  .col {
    width: 70%;
  }

  .submit {
    width: 100%;
  }

  h3 {
    text-align: center;
  }
}

@media only screen and (max-width: 480px) {
  .topBody > h1 {
    font-size: 2em;
    width: 96%;
  }
  .topBody > p {
    font-size: 1em;
    width: 95%;
    margin-top: 20px;
  }

  .col {
    width: 80%;
  }

  select{
    width: 100%;
  }

  .submit {
    width: 100%;
  }

  .subtotal > input {
    margin-bottom: 10px;
  }

  h3 {
    text-align: center;
  }
}
